import React, { useState } from "react";
import Header from "../components/Header";
import * as Our from "../components/Core";
import { auth, useSession } from "../services/firebase";
import { gql, useQuery, useMutation } from "@apollo/client";
import InputPlace from "../components/InputPlace";
import Spinner from "../components/Spinner";
import { useHistory } from "react-router";
import InputSeed from "../components/InputSeed";

const ME = gql`
  query Me {
    me {
      id
      seeds {
        name
        id
        date
        count
        isPublic
        category
        waterConditions
        soilConditions
        sunConditions
        place {
          id
          name
        }
      }
      places {
        id
        name
        address
      }
    }
  }
`;

const ADD_PLACE = gql`
  mutation AddPlace($input: AddPlaceInput) {
    addPlace(input: $input) {
      id
    }
  }
`;

const ADD_SEED = gql`
  mutation AddSeed($input: AddSeedInput) {
    addSeed(input: $input) {
      id
    }
  }
`;

const UPDATE_SEED = gql`
  mutation UpdateSeed($id: ID!, $patch: UpdateSeedInput) {
    updateSeed(id: $id, patch: $patch) {
      id
    }
  }
`;

const REMOVE_SEED = gql`
  mutation RemoveSeed($id: ID!) {
    removeSeed(id: $id) {
      id
    }
  }
`;

export default function MyAccount() {
  const userSession = useSession();

  const [selectedPlace, setSelectedPlace] = useState("all");
  const [showAddSeed, setShowAddSeed] = useState(false);
  const [showEditSeed, setShowEditSeed] = useState(false);
  const [seedToEdit, setSeedToEdit] = useState(undefined);

  const [updateSeed] = useMutation(UPDATE_SEED);
  const [addSeed] = useMutation(ADD_SEED);
  const [removeSeed] = useMutation(REMOVE_SEED);

  const {
    data: userData,
    loading: loadingUserData,
    error: userError,
  } = useQuery(ME);

  const filteredSeeds = userData?.me.seeds.filter((seed) => {
    if (selectedPlace === "all") return seed;
    return seed.place?.id === selectedPlace;
  });

  const hasMorePlaces = userData?.me.places.length > 1;

  return (
    <>
      <Header user={userSession} onSignOut={() => auth.signOut()}></Header>
      <Our.Container center size="md">
        <Our.Box padding-x="lg" margin-y="xl">
          <Our.Flex align-items="center" justify-content="between">
            <Our.Text inline weight="500" tag="h1">
              My Seeds
            </Our.Text>
            <Our.Button
              rounded
              type="transparent"
              onClick={() => setShowAddSeed(true)}
            >
              <i className="gg-math-plus"></i>
            </Our.Button>
          </Our.Flex>
        </Our.Box>
        {hasMorePlaces && (
          <Our.Box padding-x="lg" margin-y="xl">
            <Our.Tabs
              full
              onChange={(e) => setSelectedPlace(e.target.value)}
              value={selectedPlace}
            >
              <Our.Tab value="all">All</Our.Tab>
              {userData?.me.places.map((place) => {
                return (
                  <Our.Tab key={place.id} value={place.id}>
                    {place.name}
                  </Our.Tab>
                );
              })}
            </Our.Tabs>
          </Our.Box>
        )}
        <Our.Box margin-y="lg">
          {userError && (
            <Our.Box margin-y="md">
              <Our.Text color="danger">{userError.message}</Our.Text>
            </Our.Box>
          )}
          {loadingUserData && (
            <Our.Flex justify-content="center" align-items="center">
              <Spinner></Spinner>
            </Our.Flex>
          )}
          {filteredSeeds && (
            <Our.Box padding-x="lg">
              <Our.List size="xl" bordered>
                {filteredSeeds.map((seed) => {
                  return (
                    <Our.ListItem key={seed.id}>
                      <div>
                        <Our.Text full weight="400" tag="h3">
                          {seed.name}
                        </Our.Text>
                        <Our.Text full weight="400" tag="p">
                          {seed.place?.address}
                        </Our.Text>
                      </div>

                      <Our.Button
                        slot="end"
                        size="sm"
                        onClick={() => {
                          setSeedToEdit(seed);
                          setShowEditSeed(true);
                        }}
                      >
                        Edit
                      </Our.Button>
                      <Our.Button
                        slot="end"
                        size="sm"
                        onClick={() => {
                          removeSeed({
                            variables: { id: seed.id },
                            refetchQueries: [{ query: ME }],
                          });
                        }}
                      >
                        Delete
                      </Our.Button>
                    </Our.ListItem>
                  );
                })}
              </Our.List>
            </Our.Box>
          )}
          <Our.Box padding-x="lg" margin-y="lg">
            <Our.Button
              rounded
              full
              type="transparent"
              onClick={() => setShowAddSeed(true)}
            >
              <i slot="start" className="gg-math-plus"></i>
              Add seed
            </Our.Button>
          </Our.Box>
        </Our.Box>
      </Our.Container>
      <Our.Modal
        open={showEditSeed}
        onToggle={(e) => setShowEditSeed(e.target.open)}
      >
        <Our.Text slot="header-start" inline tag="h3">
          Edit seed
        </Our.Text>
        <Our.Button
          slot="header-end"
          onClick={() => {
            setShowEditSeed(false);
          }}
          rounded
          size="sm"
          squared
          type="transparent"
        >
          <i className="gg-close"></i>
        </Our.Button>

        <InputSeed
          initialState={seedToEdit}
          places={userData?.me.places}
          onSubmit={(input) => {
            setShowEditSeed(false);
            updateSeed({
              variables: { id: seedToEdit.id, patch: input },
              refetchQueries: [
                {
                  query: ME,
                },
              ],
            });
          }}
        ></InputSeed>
      </Our.Modal>
      <Our.Modal
        open={showAddSeed}
        onToggle={(e) => setShowAddSeed(e.target.open)}
      >
        <Our.Box slot="header">
          <Our.Flex justify-content="between" align-items="center">
            <Our.Text inline tag="h3">
              Add seed
            </Our.Text>
            <Our.Button
              onClick={() => {
                setShowAddSeed(false);
              }}
              rounded
              size="sm"
              squared
              type="transparent"
            >
              <i className="gg-close"></i>
            </Our.Button>
          </Our.Flex>
        </Our.Box>
        <InputSeed
          resetOnSubmit
          places={userData && userData.me.places}
          initialState={{
            name: "",
            count: 10,
            isPublic: false,
            date: "",
            place: {
              id: selectedPlace === "all" ? "" : selectedPlace,
              name: "",
            },
            sunConditions: [],
            waterConditions: [],
            soilConditions: [],
            temperatureRange: { start: 10, end: 30 },
          }}
          onSubmit={(input) => {
            setShowAddSeed(false);
            addSeed({
              variables: { input: { ...input, userId: userSession.uid } },
              refetchQueries: [{ query: ME }],
            });
          }}
        ></InputSeed>
      </Our.Modal>
    </>
  );
}
