import React from "react";
import Header from "../components/Header";
import * as Our from "../components/Core";
import { auth, useSession } from "../services/firebase";
import { gql, useQuery, useMutation } from "@apollo/client";

import SeedCard from "../components/SeedCard";
import Spinner from "../components/Spinner";

const ME = gql`
  query Me {
    me {
      bookmarkedSeeds {
        id
        name
        count
        place {
          address
          id
        }
      }
    }
  }
`;

const REMOVE_SEED_BOOKMARK = gql`
  mutation removeSeedBookmark($id: ID!) {
    removeSeedBookmark(id: $id) {
      id
    }
  }
`;

export default function MyAccount() {
  const userSession = useSession();

  const { data: myData, loading: loadingMyData } = useQuery(ME);

  const [removeBookmark] = useMutation(REMOVE_SEED_BOOKMARK);

  return (
    <>
      <Header user={userSession} onSignOut={() => auth.signOut()}></Header>
      <Our.Container center size="md">
        <Our.Box padding-x="lg" margin-y="xl">
          <Our.Text weight="500" tag="h1">
            Saved
          </Our.Text>
        </Our.Box>
      </Our.Container>

      <Our.Container center size="md">
        <Our.Box padding-x="lg">
          {loadingMyData && (
            <Our.Flex justify-content="center" align-items="center">
              <Spinner></Spinner>
            </Our.Flex>
          )}
          {myData && !myData.me.bookmarkedSeeds.length && (
            <div>No bookmarks yet..</div>
          )}
          {myData?.me.bookmarkedSeeds.map((seed) => (
            <SeedCard
              isAuthenticated={userSession.uid}
              isBookmarked={true}
              onRemoveBookmark={() => {
                removeBookmark({
                  variables: {
                    id: seed.id,
                  },
                  optimisticResponse: {
                    __typename: "Mutation",
                    removeSeedBookmark: {
                      id: seed.id,
                      __typename: "Seed",
                    },
                  },
                  update(cache, { data: { removeSeedBookmark } }) {
                    const { me } = cache.readQuery({
                      query: ME,
                      variables: { id: userSession.uid },
                    });
                    cache.writeQuery({
                      query: ME,
                      variables: { id: userSession.uid },
                      data: {
                        me: {
                          ...me,
                          bookmarkedSeeds: me.bookmarkedSeeds.filter(
                            (b) => b.id !== removeSeedBookmark.id
                          ),
                        },
                      },
                    });
                  },
                });
              }}
              key={seed.id}
              seed={seed}
            ></SeedCard>
          ))}
        </Our.Box>
      </Our.Container>
    </>
  );
}
