import React, { useState } from "react";
import Header from "../components/Header";
import * as Our from "../components/Core";
import { auth, useSession } from "../services/firebase";
import { useQuery, gql } from "@apollo/client";
import { useHistory } from "react-router";

const ME = gql`
  query Me {
    me {
      id
      places {
        id
        name
      }
    }
  }
`;

export default function MyAccount() {
  const history = useHistory();
  const userSession = useSession();
  const [darkMode, setDarkMode] = useState(document.body.hasAttribute("mode"));

  const { data: userData, loading: loadingUser, error: userError } = useQuery(
    ME
  );

  function toggleMode(checked) {
    if (!checked) {
      setDarkMode(false);
      document.body.removeAttribute("mode");
    } else {
      document.body.setAttribute("mode", "dark");
      setDarkMode(true);
    }
  }

  return (
    <>
      <Header user={userSession} onSignOut={() => auth.signOut()}></Header>
      <Our.Container center size="md">
        <Our.Box padding-x="lg" margin-y="xl">
          <Our.Text weight="500" inline tag="h1">
            Account
          </Our.Text>
        </Our.Box>
        <Our.Box padding-x="lg" margin-y="md">
          <Our.Label>Settings</Our.Label>
          <Our.List size="xl" bordered>
            <Our.ListItem onClick={() => history.push("/my-places")} selectable>
              My places {userData ? `(${userData.me.places.length})` : null}
              <i slot="end" className="gg-chevron-right"></i>
            </Our.ListItem>
            <Our.ListItem>
              Dark mode
              <Our.Toggle
                slot="end"
                full
                checked={darkMode}
                onChange={(e) => toggleMode(e.target.checked)}
              ></Our.Toggle>
            </Our.ListItem>
            <Our.ListItem onClick={() => auth.signOut()}>Logout</Our.ListItem>
          </Our.List>
        </Our.Box>
      </Our.Container>
    </>
  );
}
