import React, { useState, useEffect } from "react";
import * as Our from "./Core";
import StepInput from "./StepInput";
import enums from "../constants/enums";

export default function InputSeed({
  submitButtonText = "Save",
  resetOnSubmit = false,
  places = [],
  initialState = {
    name: "",
    count: 10,
    isPublic: false,
    date: "",
    place: { id: "", name: "" },
    category: "",
    sunConditions: [],
    waterConditions: [],
    soilConditions: [],
    temperatureRange: { start: 10, end: 30 },
  },
  onSubmit,
}) {
  const [input, setInput] = useState(initialState);

  const hasMorePlaces = places.length > 1;

  useEffect(() => {
    setInput(initialState);
  }, [JSON.stringify(initialState)]);

  function handleOnSubmit() {
    const { __typename, id, place, ...data } = input;
    // if user has more places, use the place id from the select input
    // is user only has one place just use the one place
    onSubmit({ ...data, placeId: hasMorePlaces ? place.id : places[0].id });
    if (resetOnSubmit) setInput(initialState);
  }

  return (
    <>
      <Our.Container size="xs" center>
        <Our.Box margin-y="md">
          <Our.Label>Name:</Our.Label>
          <Our.Input
            value={input.name}
            onInput={(e) => setInput({ ...input, name: e.target.value })}
            full
          >
            <div slot="error">Field is required</div>
          </Our.Input>
        </Our.Box>
        <Our.Box margin-y="md">
          <Our.Label>Category:</Our.Label>
          <Our.Select
            value={input.category}
            onChange={(e) =>
              setInput({
                ...input,
                category: e.target.value,
              })
            }
            full
          >
            {enums.seedCategories.map((cat) => (
              <Our.Option value={cat} key={cat}>
                {cat}
              </Our.Option>
            ))}
          </Our.Select>
        </Our.Box>
        <Our.Box margin-y="md">
          <Our.Label>Date:</Our.Label>
          <Our.Input
            type="date"
            value={input.date}
            onInput={(e) => setInput({ ...input, date: e.target.value })}
            full
          ></Our.Input>
        </Our.Box>
        {hasMorePlaces && (
          <Our.Box margin-y="md">
            <Our.Label>Place:</Our.Label>
            <Our.Select
              value={input.place?.id}
              onChange={(e) =>
                setInput({
                  ...input,
                  place: { ...input.place, id: e.target.value },
                })
              }
              full
            >
              {places.map((place) => (
                <Our.Option
                  selected={place.id === input.place?.id}
                  value={place.id}
                  label={place.name}
                  key={place.id}
                ></Our.Option>
              ))}
            </Our.Select>
          </Our.Box>
        )}
        <Our.Box margin-y="md">
          <Our.Label>Soil Conditions:</Our.Label>
          <Our.Select
            multiple
            value={input.soilConditions}
            onChange={(e) =>
              setInput({
                ...input,
                soilConditions: e.target.value.filter((v) => v !== ""),
              })
            }
            full
          >
            {enums.soilConditions.map((soil) => (
              <Our.Option value={soil} key={soil}>
                {soil}
              </Our.Option>
            ))}
          </Our.Select>
        </Our.Box>

        <Our.Box margin-y="md">
          <Our.Label>Sun Conditions:</Our.Label>
          <Our.Select
            multiple
            value={input.sunConditions}
            onChange={(e) =>
              setInput({
                ...input,
                sunConditions: e.target.value.filter((v) => v !== ""),
              })
            }
            full
          >
            {enums.sunConditions.map((sun) => (
              <Our.Option value={sun} key={sun}>
                {sun}
              </Our.Option>
            ))}
          </Our.Select>
        </Our.Box>
        <Our.Box margin-y="md">
          <Our.Label>Water Conditions:</Our.Label>
          <Our.Select
            multiple
            value={input.waterConditions}
            onChange={(e) =>
              setInput({
                ...input,
                waterConditions: e.target.value.filter((v) => v !== ""),
              })
            }
            full
          >
            {enums.waterConditions.map((water) => (
              <Our.Option value={water} key={water}>
                {water}
              </Our.Option>
            ))}
          </Our.Select>
        </Our.Box>
        <Our.Box margin-y="md">
          <Our.Label>Count:</Our.Label>
          <StepInput
            value={input.count}
            onChange={(val) => setInput({ ...input, count: val })}
          ></StepInput>
        </Our.Box>
        <Our.Box margin-y="md">
          <Our.Label>Public:</Our.Label>
          <Our.Toggle
            checked={input.isPublic}
            onChange={(e) => setInput({ ...input, isPublic: e.target.checked })}
          ></Our.Toggle>
        </Our.Box>
        <Our.Box margin-y="md">
          <Our.Button full type="primary" onClick={() => handleOnSubmit()}>
            {submitButtonText}
          </Our.Button>
        </Our.Box>
      </Our.Container>
    </>
  );
}
