import React, { useState, useEffect } from "react";
import * as Our from "./Core";
import enums from "../constants/enums";

export default function InputFilterSeeds({
  initialState = {
    category: "",
    soilConditions: [],
    sunConditions: [],
    waterConditions: [],
    temperatureRange: { start: 0, end: 40 },
  },
  resetOnSubmit = false,
  isLoading = false,
  onSubmit = () => {},
  submitButtonText = "Save",
}) {
  const [input, setInput] = useState(initialState);

  useEffect(() => {
    setInput(initialState);
  }, [JSON.stringify(initialState)]);

  function handleSetInput(newInput) {
    setInput(newInput);
  }

  function handleOnSubmit() {
    const { __typename, id, ...data } = input;
    onSubmit(data);

    if (resetOnSubmit) setInput(initialState);
  }

  return (
    <>
      <Our.Container size="xs" center>
        <Our.Box margin-y="md">
          <Our.Box margin-y="md">
            <Our.Label>What are you looking for?</Our.Label>
            <Our.Select
              input={input.category}
              onSelect={(e) => {
                handleSetInput({
                  ...input,
                  category: e.target.value,
                });
              }}
              full
            >
              {enums.seedCategories.map((option) => (
                <Our.Option
                  key={option}
                  value={option}
                  label={option}
                ></Our.Option>
              ))}
            </Our.Select>
          </Our.Box>
          <Our.Label>Sun conditions</Our.Label>
          <Our.Select
            full
            multiple
            input={input.sunConditions?.toString()}
            onSelect={(e) => {
              handleSetInput({
                ...input,
                sunConditions: e.target.value,
              });
            }}
          >
            {enums.sunConditions.map((option) => (
              <Our.Option key={option} value={option}></Our.Option>
            ))}
          </Our.Select>
        </Our.Box>
        <Our.Box margin-y="md">
          <Our.Label>Soil conditions</Our.Label>
          <Our.Select
            full
            multiple
            value={input.soilConditions}
            onSelect={(e) => {
              handleSetInput({
                ...input,
                soilConditions: e.target.value,
              });
            }}
          >
            {enums.soilConditions.map((option) => (
              <Our.Option key={option} value={option}></Our.Option>
            ))}
          </Our.Select>
        </Our.Box>
        <Our.Box margin-y="md">
          <Our.Label>Water requirement</Our.Label>
          <Our.Range
            full
            labels="Low,Medium,High"
            sensitivity="0.7"
            value="0,4"
            max="4"
            min="0"
            step="1"
          ></Our.Range>
        </Our.Box>
        <Our.Box margin-y="md">
          <Our.Label>Temperature</Our.Label>
          <Our.Range
            full
            labels="0,10,20,30,40"
            sensitivity="0.7"
            value="0,40"
            max="40"
            min="-0"
            step="5"
          ></Our.Range>
        </Our.Box>
        <Our.Button full type="primary" onClick={() => handleOnSubmit()}>
          {submitButtonText}
        </Our.Button>
      </Our.Container>
    </>
  );
}
