import React, { useState } from "react";
import { Link, NavLink } from "react-router-dom";
import "./Header.css";
import * as Our from "./Core";
import InputSignUp from "./InputSignUp";
import InputSignIn from "./InputSignIn";
import { useMutation, gql } from "@apollo/client";

const SIGN_IN = gql`
  mutation SignIn($input: SignInInput!) {
    signIn(input: $input) {
      id
    }
  }
`;

const SIGN_UP = gql`
  mutation SignUp($input: SignUpInput!) {
    signUp(input: $input) {
      id
    }
  }
`;

export default function Header({ user }) {
  const [tab, setTab] = useState("signin");
  const [showModal, setShowModal] = useState(false);

  const [signIn, { error: signInError }] = useMutation(SIGN_IN);
  const [signUp, { error: signUpError }] = useMutation(SIGN_UP);

  return (
    <header className="header">
      <Our.Text className="logo" weight="600" look="p">
        <Link to="/">
          <span style={{ color: "var(--core-color-primary)" }}>Our</span> Seeds
        </Link>
      </Our.Text>
      <nav>
        <NavLink exact to="/">
          <i slot="start" className="gg-home"></i>
          <span>Home</span>
        </NavLink>
        <NavLink exact to="/search">
          <i slot="start" className="gg-search"></i>
          <span>Find</span>
        </NavLink>
        {user ? (
          <>
            <NavLink to="/my-listings">
              <i slot="start" className="gg-add"></i>
              <span>Add</span>
            </NavLink>
            <NavLink to="/my-bookmarks">
              <i slot="start" className="gg-bookmark"></i>
              <span>Saved</span>
            </NavLink>
            <NavLink to="/my-account">
              <i slot="start" className="gg-profile"></i>
              <span>Profile</span>
            </NavLink>
          </>
        ) : (
          <>
            <a
              href="/"
              onClick={(e) => {
                e.preventDefault();
                setShowModal(true);
              }}
            >
              <i slot="start" className="gg-profile"></i>
              <span>Login</span>
            </a>
          </>
        )}
      </nav>
      <Our.Modal open={showModal} onToggle={(e) => setShowModal(e.target.open)}>
        <Our.Container size="xxs" center>
          <Our.Box margin-y="lg">
            <Our.Tabs
              full
              position="center"
              value={tab}
              onChange={(e) => setTab(e.target.value)}
            >
              <Our.Tab value="signin">Login</Our.Tab>
              <Our.Tab value="signup">Sign Up</Our.Tab>
            </Our.Tabs>
          </Our.Box>
          {tab === "signup" && (
            <InputSignUp
              error={signUpError}
              onSubmit={(input) => {
                signUp({
                  variables: {
                    input: {
                      email: input.email,
                      password: input.password,
                    },
                  },
                  onComplete: () => {
                    console.log("completed");
                    setShowModal(false);
                  },
                }).then((res) => setShowModal(false));
              }}
            ></InputSignUp>
          )}
          {tab === "signin" && (
            <InputSignIn
              error={signInError}
              onSubmit={(input) => {
                signIn({
                  variables: {
                    input: {
                      email: input.email,
                      password: input.password,
                    },
                  },
                }).then((res) => setShowModal(false));
              }}
            ></InputSignIn>
          )}
        </Our.Container>
      </Our.Modal>
    </header>
  );
}
