import React, { useState } from "react";
import { useParams, useHistory } from "react-router-dom";
import Header from "../components/Header";
import * as Our from "../components/Core";
import { useSession, auth } from "../services/firebase";
import { useMutation, gql, useQuery } from "@apollo/client";
import Spinner from "../components/Spinner";
import InputPlace from "../components/InputPlace";

const ME = gql`
  query Me {
    me {
      id
      places {
        id
        name
        address
      }
    }
  }
`;

const ADD_PLACE = gql`
  mutation RemovePlace($input: AddPlaceInput!) {
    addPlace(input: $input) {
      id
    }
  }
`;

const UPDATE_PLACE = gql`
  mutation UpdatePlace($id: ID!, $patch: UpdatePlaceInput!) {
    updatePlace(id: $id, patch: $patch) {
      id
    }
  }
`;

const REMOVE_PLACE = gql`
  mutation RemovePlace($id: ID!) {
    removePlace(id: $id) {
      id
    }
  }
`;

export default function MyPlaces() {
  const history = useHistory();
  const userSession = useSession();

  const [placeToEdit, setPlaceToEdit] = useState(undefined);
  const [showAddPlace, setShowAddPlace] = useState(false);
  const [showEditPlace, setShowEditPlace] = useState(false);

  const [addPlace] = useMutation(ADD_PLACE);
  const [updatePlace] = useMutation(UPDATE_PLACE);
  const [removePlace] = useMutation(REMOVE_PLACE);

  const { data: userData, loading: loadingUser, error: userError } = useQuery(
    ME
  );

  return (
    <>
      <Header
        user={userSession}
        onSignOut={() => {
          auth.signOut();
        }}
      ></Header>
      <Our.Container center size="md">
        <Our.Box padding-x="md" margin-y="md">
          <Our.Button
            rounded
            onClick={() => history.push("/my-account")}
            type="transparent"
          >
            <i slot="start" className="gg-arrow-left"></i> My account
          </Our.Button>
        </Our.Box>
        <Our.Box padding-x="lg" margin-y="xl">
          <Our.Flex align-items="center" justify-content="between">
            <Our.Text weight="500" tag="h1">
              My Places
            </Our.Text>
            <Our.Button
              rounded
              onClick={() => setShowAddPlace(true)}
              type="transparent"
            >
              <i className="gg-math-plus"></i>
            </Our.Button>
          </Our.Flex>
        </Our.Box>
      </Our.Container>

      <Our.Container center size="md">
        <Our.Box padding-x="lg" padding-y="lg">
          {userError && (
            <Our.Box margin-y="md">
              <Our.Text color="danger">{userError.message}</Our.Text>
            </Our.Box>
          )}
          {loadingUser && (
            <Our.Flex justify-content="center" align-items="center">
              <Spinner></Spinner>
            </Our.Flex>
          )}
          {userData?.me.places.length > 0 && (
            <Our.List selectable bordered size="xl">
              {userData.me.places.map((place, i) => {
                return (
                  <Our.ListItem key={place.id}>
                    <Our.Box padding-y="lg">
                      <Our.Text weight="400" tag="h2">
                        {place.name}
                      </Our.Text>
                      <Our.Text tag="p">{place.address}</Our.Text>
                    </Our.Box>
                    <Our.Button
                      onClick={() => {
                        setPlaceToEdit(place);
                        setShowEditPlace(true);
                      }}
                      size="sm"
                      type="transparent"
                      slot="end"
                    >
                      Edit
                    </Our.Button>
                    <Our.Button
                      onClick={() =>
                        removePlace({
                          variables: {
                            id: place.id,
                          },
                          refetchQueries: [{ query: ME }],
                        })
                      }
                      size="sm"
                      type="transparent"
                      slot="end"
                    >
                      Delete
                    </Our.Button>
                  </Our.ListItem>
                );
              })}
            </Our.List>
          )}
          <Our.Box margin-y="lg">
            <Our.Button
              rounded
              full
              type="transparent"
              onClick={() => setShowAddPlace(true)}
            >
              <i slot="start" className="gg-math-plus"></i>
              Add place
            </Our.Button>
          </Our.Box>
        </Our.Box>
      </Our.Container>
      <Our.Modal
        open={showEditPlace}
        onToggle={(e) => setShowEditPlace(e.target.open)}
      >
        <Our.Text slot="header-start" inline tag="h3">
          Edit Place
        </Our.Text>
        <Our.Button
          slot="header-end"
          onClick={() => {
            setShowEditPlace(false);
          }}
          rounded
          size="sm"
          squared
          type="transparent"
        >
          <i className="gg-close"></i>
        </Our.Button>

        <InputPlace
          initialState={placeToEdit}
          onSubmit={(input) => {
            setShowEditPlace(false);
            updatePlace({
              variables: {
                id: placeToEdit.id,
                patch: { ...input },
              },
              refetchQueries: [{ query: ME }],
            });
          }}
        ></InputPlace>
      </Our.Modal>
      <Our.Modal
        open={showAddPlace}
        onToggle={(e) => setShowAddPlace(e.target.open)}
      >
        <Our.Text slot="header-start" inline tag="h3">
          Add Place
        </Our.Text>
        <Our.Button
          slot="header-end"
          onClick={() => {
            setShowAddPlace(false);
          }}
          rounded
          size="sm"
          squared
          type="transparent"
        >
          <i className="gg-close"></i>
        </Our.Button>

        <InputPlace
          resetOnSubmit
          onSubmit={(input) => {
            setShowAddPlace(false);
            addPlace({
              variables: { input: { ...input, userId: userSession.uid } },
              refetchQueries: [{ query: ME }],
            });
          }}
        ></InputPlace>
      </Our.Modal>
    </>
  );
}
